import Vue from 'vue';
import Vuex from 'vuex';

/*Vuex  store getter store 
getter */


/*Action  mutation
Action  mutation
Action */


Vue.use(Vuex);



export default new Vuex.Store({
	state:{
		del:false,
		totalPrice: 900,
    allChecked:false,
		totalNum: 3,
		added:[],
		buy:[
			{
        checked:false,
				id:0,
				imag:'./data/images/details05.png',
				name:"adidas Alpha Bounce Beyond",
				desc:"",
				size:45,
				num:1,
				price:400
			},
			{
        checked:false,
				id:1,
				imag:'./data/images/home008.jpg',
				name:"Converse 1970s High White Hi",
				desc:"",
				size:44,
				num:1,
				price:300
			},
			{
        checked:false,
				id:2,
				imag:'./data/images/home009.jpg',
				name:"Anta White Hi",
				desc:"",
				size:43,
				num:1,
				price:200
			}
		]
	},
	mutations:{
		add_cart(state, id) {

		    state.buy[id].num++

		    state.totalNum++

		    state.totalPrice += state.buy[id].price

		 },
		reducec_cart(state, id) {

		    if (state.buy[id].num > 0) {

			    state.buy[id].num--

			    state.totalNum--

			    state.totalPrice -= state.buy[id].price

		    }
		},
		clearAll(state,id){

			state.totalPrice=0

			state.buy[id]=''
		},
		del(state,id){
      state.totalPrice -= state.buy[id].num*state.buy[id].price
      state.totalNum -= state.buy[id].num
			state.buy.splice(id,1)

		},
		todel(state){
			state.del=!state.del
		},

    allCheckedtrue(state){
      state.allChecked = true;
      console.log(state.allChecked)
    },
    allCheckedfalse(state){
      state.allChecked = false;
      console.log(state.allChecked)
    }

	}


})



